.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.pos-val{
  color:green;
}

.neg-val{
  color: red;
}

.percenteTotal{
  font-size: 40px;
}

.percenteValue{
  height: 150px;
  width: 200px;
  font-size: 72px;
  text-align: center;
  border-radius: 20px;
  border: 1px solid #000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
